.general-hero{
    min-height: 450px;
    display: flex;
    align-items: center;
    h2{
        color: #fff;
        font-size: 48px;
    }
}

.section-contact{
    padding: 120px 0;
    h2{
        line-height: 1.407;
    }
    .contact-side{
        margin-top: -235px;
        background: #fff;
        padding: 50px 40px;
        h4{
            letter-spacing: 0.2em;
            margin-bottom: 25px;
        }
        .location{
            padding-bottom: 60px;
        }
        .business{
            padding-bottom: 50px;
        }
        .inquiries{
            padding-bottom: 50px;
        }
    }
    .socials{
        a{
            color: #1f619b;
            i{
                font-size: 50px;
                margin-right: 12px;
            }
        }
    }
    input{
        background-color: #f4f7f9;
        border: none;
        margin-bottom: 25px;
    }
    textarea{
        background-color: #f4f7f9;
        border: none;
        margin-bottom: 25px;
    }
    form{
        margin-top: 50px;
    }
    .content-wrap{
        max-width: 720px;
    }
}
