/* Hero */
.hero--blog {
  @include backgroundImage(center, $color-blue, cover);
  min-height: 529px;
  &__content {
    max-width: 616px;
    margin: 0 auto;
    padding-top: 130px;
    text-align: center;
    h5 {
      text-transform: uppercase;
      @include font-responsive(20, 18);
      font-weight: 300;
      color: #7abced;
      margin-bottom: 38px;
    }
    h2 {
      @include font-responsive(48, 30);
      font-weight: 700;
      line-height: 60px;
      color: $color-white;
    }
  }
}

// Featured
.section--featured {
  margin-top: -154px;
  .col-md-6 {
    padding: 0;
  }
  &__text {
    padding: 65px;
    background-color: #1f619b;
    color: $color-white;
    h6 {
      color: #d2d7d7;
      text-transform: uppercase;
      font-size: 16px;
      font-weight: 300;
      letter-spacing: 4px;
      margin-bottom: 25px;
    }
    h2 {
      @include font-responsive(33, 25);
      font-weight: 600;
      line-height: 40px;
      margin-bottom: 25px;
    }
    p {
      @include font-responsive(18, 16);
      font-weight: 500;
      line-height: 28px;
      margin-bottom: 70px;
    }
    a {
      color: $color-white;
      text-decoration: none;
      font-size: 20px;
      font-weight: 600;
      display: flex;
      align-items: center;
      i {
        font-size: 25px;
        margin-left: 8px;
      }
    }
  }
  &__img {
    @include backgroundImage(center, $color-blue, cover);
    width: 100%;
    min-height: 100%;
  }
}

// Blogs
.list--blog {
  padding: 128px 0 0;
  .container {
    max-width: 1006px;
  }
  .col-md-6,
  .col-md-12{
    margin-bottom: 90px;
  }
  .blog--item {
    &__img {
      @include backgroundImage(center, $color-blue, cover);
      width: 100%;
      min-height: 487px;
    }
    &__content {
      .item__meta {
        font-size: 18px;
        color: #1f609a;
        margin-bottom: 30px;
      }
      h3 {
        @include font-responsive(33, 25);
        font-weight: 600;
        line-height: 40px;
        margin-bottom: 25px;
      }
      p {
        font-size: 18px;
        font-weight: 600;
        line-height: 28px;
      }
      a {
        text-decoration: none;
        font-size: 20px;
        font-weight: 600;
        display: flex;
        align-items: center;
        margin-bottom: 56px;
        i {
          font-size: 25px;
          margin-left: 8px;
        }
      }
    }
  }
  .blog--spacing1{
    padding-right: 68px;
    .blog--item__content{
      padding-top: 40px;
    }
  }
  .blog--spacing2{
    padding-left: 68px;
  }
  .blog--spacing3{
    padding-right: 68px;

  }
  .blog--spacing4{
    padding-left: 68px;
    .blog--item__content{
      padding-top: 40px;
    }
  }
  @media (max-width: $small-tablet ){
    .blog--spacing1{
      padding-right: 0px;
      
    }
    .blog--spacing2{
      padding-left: 0px;
    }
    .blog--spacing3{
      padding-right: 0px;
  
    }
    .blog--spacing4{
      padding-left: 0px;
     
    }
  }
  
  &__bg{
    background-color: $body-bkg2;
    min-height: 236px;
  }
}
