  line-height: 20px;
  margin-bottom: 10px;
  margin-top: 10px;
  padding: 15px 0;
  position: relative;
  z-index: 1000; }

.menu-toggle.activated::before {
  content: "\f335"; }

.site-header .menu-toggle::before {
  float: left;
  margin-right: 5px;
  position: relative;
  text-rendering: auto;
  top: 1px; }

.sub-menu-toggle {
  float: right;
  padding: 9px 10px;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 100; }

.sub-menu .sub-menu-toggle {
  padding: 12px 10px; }

.sub-menu-toggle::before {
  display: inline-block;
  text-rendering: auto;
  transform: rotate(0);
  transition: transform 0.25s ease-in-out; }

.sub-menu-toggle.activated::before {
  transform: rotate(180deg); }

/* Header Menu
--------------------------------------------- */
.nav-primary {
  clear: left;
  padding-bottom: 15px;
  padding-top: 15px;
  width: 100%; }

/* Footer Menu
--------------------------------------------- */
.nav-secondary {
  margin-top: 10px; }

.nav-secondary .genesis-nav-menu {
  line-height: 1.5; }

.nav-secondary .menu-item {
  display: inline-block; }

.nav-secondary a {
  margin-left: 10px;
  margin-right: 10px;
  padding: 0; }

