// Hero
.section--hero {
  &__industry {
    @include backgroundImage(center, $color-blue, cover);
    min-height: 755px;
    .item__content {
      padding-top: 228px;
      color: $color-white;
      h2 {
        @include font-responsive(48, 30);
        font-weight: 700;
        margin-bottom: 70px;
      }
    }
  }
}

// Does
.section--does {
  padding-top: 120px;
  &__img {
    p {
      @include font-responsive(21, 18);
      line-height: 36px;
      margin-bottom: 70px;
    }
    &__bg {
      position: relative;
      img {
        position: relative;
        z-index: 1;
        min-height: 427px;
      }
      &::before {
        content: "";
        width: 72px;
        height: 110%;
        background-color: #f47d20;
        position: absolute;
        top: -22px;
        left: -25px;
        z-index: -1;
      }
    }
  }
  &__text {
    padding-left: 40px;
    h2 {
      @include font-responsive(40, 30);
      font-weight: 600;
      line-height: 48px;
      margin-bottom: 40px;
    }
    ul {
      li {
        padding-left: 30px;
        position: relative;
        @include font-responsive(18, 16);
        font-weight: 600;
        line-height: 28px;
        margin-bottom: 29px;
        &::before {
          content: "";
          position: absolute;
          top: 6px;
          left: 0;
          width: 15px;
          height: 10px;
          background-color: $color-blue;
          border-radius: 3px;
        }
      }
    }
  }
}

// Investing
.section--investing {
  background-color: $body_bkg2;
  padding: 220px 0 0;
  margin-top: -87px;
  &__head {
    max-width: 840px;
    margin: 0 auto;
    text-align: center;
    h2 {
      @include font-responsive(46, 30);
      font-weight: 500;
      margin-bottom: 40px;
      span {
        color: $color-blue;
        font-weight: 600;
      }
    }
    p {
      @include font-responsive(21, 18);
      line-height: 36px;
      margin-bottom: 70px;
    }
  }
  &__card {
    text-align: center;
    border-top: 2px solid #7baadc;
    h3 {
      padding: 34px 0;
      @include font-responsive(22, 18);
      font-weight: 700;
      margin: 0;
    }
    &__img {
      @include backgroundImage(center, $color-blue, cover);
      min-height: 284px;
    }
    p {
      padding: 32px 0 0;
      @include font-responsive(21, 18);
      line-height: 36px;
    }
  }
  &__button {
    text-align: center;
  }
}

// Testimonial
.section--testimonial {
  @include backgroundImage(center, $body_bkg2, cover);
  min-height: 653px;
  padding: 158px 0;
  .container {
    max-width: 975px;
  }
  &__text {
    height: 100%;
    display: flex;
    align-items: center;
    &--content {
      h2 {
        @include font-responsive(40, 30);
        font-weight: 600;
        line-height: 48px;
        margin-bottom: 40px;
        position: relative;
        &::before {
          content: "";
          position: absolute;
          top: -75px;
          left: 0;
          width: 65px;
          height: 49px;
          background-image: url("../../images/quote.png");
          background-size: cover;
        }
      }
    }
  }
  &__items {
    position: relative;
    &__card {
      background-color: $color-white;
      padding: 47px;
      border-radius: 10px;
      position: relative;
      p {
        @include font-responsive(18, 16);
        line-height: 28px;
        margin-bottom: 25px;
      }
      h5{
        color: $color-blue;
        @include font-responsive(20, 18);
        font-weight: 500;
        margin-bottom: 0px;
      }
      h6{
        @include font-responsive(18, 16);
        font-weight: 400;
        margin-bottom: 0px;
        font-style: italic;
      }
    
    }
    &::before{
      content: "";
      position: absolute;
      top: -30px;
      right: -30px;
      width: 100%;
      height: 119%;
      background-color: #1e5e97;
      border-radius: 10px;
    }
  }
}


// Case 
.section--case{
  padding: 190px 0;
  &__text{
    h2{
      @include font-responsive(46, 30);
      font-weight: 500;
      margin-bottom: 40px;
      span {
        color: $color-blue;
        font-weight: 600;
      }
    }
    p{
      @include font-responsive(18, 16);
      font-weight: 500;
        line-height: 28px;
        margin-bottom: 25px;
    }
    a{
      text-decoration: none;
      font-size: 20px;
      font-weight: 600;
      display: flex;
      align-items: center;
      i {
        font-size: 25px;
        margin-left: 8px;
      }
    }
  }
  &__img{
    position: relative;
    &__bg{
      @include backgroundImage(center, $color-blue, cover);
      min-height: 527px;
      &::before {
        content: "";
        width: 71px;
        height: 110%;
        background-color: #f47d20;
        position: absolute;
        top: -30px;
        right: -27px;
        z-index: -1;
      }
    }
  }
}